import { PanelConfig } from "@rp/react/tanstack-api/discovery-panel-config"

export const getAllWatchlistCategories = (
  searchTerm: string,
  categories: PanelConfig["discoveryPanelCategories"],
) => {
  if (searchTerm.length > 0) {
    return []
  }

  return categories
    .filter(item => item.watchlistFilter && !item.watchlistGroups.allExcluded)
    .map(({ label }) => label)
}
